body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
  background: #000033;
  color: white;
}
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  position: relative;
}
.line{
  position: absolute;
  left: 50%;
  height: 5px;
  width: 85%;
  background-color: #4B0082;
  border-radius: 20px;
  opacity: 0;
  box-shadow: 0 0 30px #9400fd;
}
.items {
  width: 70px;
  height: 70px;
}
.square {
  width: 70px;
  height: 70px;
  border: none;
  outline: none;
  border: 1px solid #3d7bf4;
  font-weight: bolder;
  font-size: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #0b0d21;
}
.heading {
  /* font-family: 'Orbitron', sans-serif; */
  font-family: "Anton", sans-serif;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: lighter;
  cursor: pointer;
  margin-top: 80px;
}
.status {
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  font-family: "Orbitron", sans-serif;
  letter-spacing: 1px;
  color: #3d7bf4;
  font-size: 24px;
}
.winnerclassO{
  letter-spacing: 1px;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  font-family: 'Audiowide', cursive;
  font-size: 32px; /* Adjust the font size as needed */
  color: #00ffcc; /* Neon Green */
  text-shadow: 0 0 10px #00ffcc;
}
.winnerclassX{
  letter-spacing: 1px;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  font-family: 'Audiowide', cursive;
  font-size: 32px; /* Adjust the font size as needed */
  color: #c0c0c0; /* Neon Green */
  text-shadow: 0 0 10px #c0c0c0;
}
.Draw{
  color:red;
  letter-spacing: 1px;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  font-family: 'Audiowide', cursive;
  font-size: 32px; 
  text-shadow: 0 0 10px red;
  text-transform: uppercase;
}
.btn{
  font-family: 'Orbitron', sans-serif;
  letter-spacing: 1px;
  font-weight: bolder;
  transition: .2s;
}
.btn:nth-child(1):hover{
  box-shadow: 2px 2px 20px #3d7bf4;
  letter-spacing: 2px;
}
.btn:nth-child(2):hover{
  box-shadow: 2px 2px 20px #c0c0c0;
  letter-spacing: 2px;
}
.buttons{
  gap: 20px;
  margin-top: 50px;
}

@media screen and (max-width: 250px) {
  .items {
    width: 50px;
    height: 50px;
  }
  .square {
    width: 50px;
    height: 50px;
  }
  .status{
    letter-spacing: 1px;
    font-size: 80%;
  }
  .buttons{
    gap: 20px;
  }
  .btn{
    font-size: 80%;
  }
}
